import { Button, Container, Dialog, DialogActions, DialogContent, DialogTitle, MenuItem } from '@material-ui/core';
import axios from 'axios';
import React from 'react';
import { useHistory,useParams } from 'react-router-dom';

export default function DeleteDialog(props) {
    const [open, setOpen] = React.useState(false);
    const history = useHistory();
    const { contentid } = useParams();
    const handleOnClick = () => {
        props.onClick();
        setOpen(true);
    }
    const handleClose = () => {
        setOpen(false);
    }
    const handleSubmit = () => {
        axios.get("/api/article/deletearticle/" + contentid)
            .then(res => {
                if (res.status === 200) {      
                    history.goBack();
                }
                
                console.log(res.data.message)
            }).catch(err => {
            console.log(err)
        })
    }
    return (
        <>
            <MenuItem onClick={handleOnClick}></MenuItem>
            <Dialog onClose={handleClose} open={open} maxWidth="sm" fullWidth>
                <DialogTitle></DialogTitle>
                <DialogContent>
                    <Container>
                        
                    </Container>
                </DialogContent>
                <DialogActions>
                    <Button variant='contained' color='primary' onClick={handleClose }></Button>
                    <Button variant='contained' color='secondary' onClick={handleSubmit}></Button>
                </DialogActions>
            </Dialog>
        </>
    )
}